import { ReactElement } from 'react';
import IS from 'react-infinite-scroll-component';

// Styles
import './InfiniteScroll.scss';

// Types
import { TestMetadata } from 'types';

export interface InfiniteScrollProps<T> {
  /** unique component id */
  id: string;
  /** all data */
  data: T[];
  /** function to call once reaching displayLimit */
  onLoadMore: () => void;
  /** max length of data to display before calling onLoadMore. If 0 or undefined, onLoadMore won't be called */
  displayLimit?: number;
  /** ReactElement to display when loading more data */
  loader?: ReactElement;
  /** child function called with limitedData  */
  children: (data: T[]) => ReactElement;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const InfiniteScroll = <T,>({
  id,
  data,
  onLoadMore,
  displayLimit,
  loader,
  testMetadata,
  children,
  ...otherProps
}: InfiniteScrollProps<T>): ReactElement => {
  const limitedData = displayLimit ? data.slice(0, displayLimit) : data;

  return (
    <div className="sb-infinite-scroll" id={id} {...testMetadata}>
      <IS
        dataLength={limitedData.length}
        next={onLoadMore}
        hasMore={!!displayLimit && displayLimit <= data.length}
        loader={loader || <h4>Loading...</h4>}
        scrollableTarget={id}
        scrollThreshold="200px"
        style={{ overflow: 'initial' }}
        {...otherProps}
      >
        {children(limitedData)}
      </IS>
    </div>
  );
};

export default InfiniteScroll;
